@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    background-color: #E4E4E4 !important;
}
#profile {
    filter: blur(8px);
}

button {
    word-break: keep-all;
}

input,
textarea {
    font-size: 17px !important;
}

#layout {
    width: 100%;
}
@media only screen and (min-width: 600px) {
    #layout {
        width: 400px !important;
        background: #FFFFFF;
    }
}